.feedback-form {
  max-width: 480px;
  background-color: #b2b24d;
  border: 3px solid #400c0c;
  border-radius: 8px;
  flex-direction: column;
  gap: 25px;
  margin: 0 auto;
  padding: 25px;
  display: flex;
}

.feedback-form label {
  text-align: center;
  color: #212121;
  flex-direction: column;
  margin-bottom: 16px;
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
  font-size: 22px;
  display: flex;
}

.feedback-form button {
  cursor: pointer;
  color: #fff;
  text-shadow: 0 -1px 4px #fff, 0 -2px 10px #ff0, 0 -10px 20px #ff8000, 0 -18px 40px red;
  background: #333;
  border-radius: 4px;
  padding: 8px 12px;
  font-family: Courier New, Courier, monospace;
  font-weight: 700;
}

input, textarea {
  font: inherit;
  background-color: #dbc7c7;
  border: 2px solid #400c0c;
  border-radius: 5px;
  outline: none;
  padding: 8px;
  font-size: 18px;
  display: block;
}

textarea {
  resize: none;
}

